<template>
    <div>
        <div v-if="mode === 'DESIGN'">
            <el-date-picker size="medium" disabled :type="type" :placeholder="placeholder"></el-date-picker>
        </div>
        <div v-else>
            <el-time-picker style="width: 100%;"
                            arrow-control
                            v-if="dateType=='time'"
                            :value-format="format"
                            :format="format"
                            :disabled="perm!='E'"
                            size="medium"
                            v-model="_value"
            >
            </el-time-picker>
            <el-date-picker :disabled="perm!='E'" style="width: 100%;" v-if="dateType=='datetime'" v-model="_value"
                            :value-format="format" size="medium" clearable type="datetime"
                            :placeholder="placeholder"></el-date-picker>

            <el-date-picker :disabled="perm!='E'" style="width: 100%;" v-if="dateType=='date'" v-model="_value"
                            :value-format="format" size="medium" clearable type="date"
                            :placeholder="placeholder"></el-date-picker>
        </div>
    </div>
</template>

<script>
import componentMinxins from '../ComponentMinxins'

export default {
    mixins: [componentMinxins],
    name: "DateTime",
    components: {},
    props: {
        value: {
            default: null
        },
        format: {
            type: String,
            default: 'yyyy-MM-dd HH:mm'
        },

        dateType: {
            type: String,
            default: 'date'
        },
        placeholder: {
            type: String,
            default: '请选择日期时间'
        },
        perm: {
            type: String,
            default: 'E'
        }
    },
    computed: {
        type() {
            switch (this.format) {
                case 'yyyy':
                    return 'year';
                case 'yyyy-MM':
                    return 'month';
                case 'yyyy-MM-dd':
                    return 'date';
                case 'yyyy-MM-dd HH:mm':
                    return 'datetime';
                default:
                    return 'datetime';
            }
        }
    },
    data() {
        return {}
    },
    methods: {}
}
</script>

<style scoped>

</style>
